<template>
  <div>
    <div class="shiyongfooter" v-for="item of list.filter(item => item.id === this.$route.params.id)" :key="item.id">
      <div class="left-footer">
        <div class="shouye" @click="toshouye">
          <span class="iconfont icon-shouye">&#xe61a;</span>
          <p>首页</p>
        </div>
        <div class="kefu">
          <span class="iconfont icon-kefu">&#xe637;</span>
          <p>客服</p>
        </div>
        <div class="shoucang" v-if="iscollect" @click="shoucangclick">
          <span class="iconfont">&#xe639;</span>
          <p>收藏</p>
        </div>
        <div class="shoucang" v-else @click="shoucangclick">
          <span class="iconfont icon-shc">&#xe604;</span>
          <p>已收藏</p>
        </div>
      </div>
      <div class="jinbiduihuan" v-if="item.shengyujinbiduihuan === '0' ? false : true">金币兑换</div>
      <div class="jinbiduihuan yiduiwan" v-else>已兑完</div>
      <div class="mianfeishenqing" @click="mianfeiclick">免费申请</div>
    </div>
    <shoucang v-show="showshoucang" :title='shoucangtitle(mes)'></shoucang>
  </div>
</template>
<script>
import shoucang from '@/pages/common/shoucang'
import { mapMutations } from 'vuex'
export default {
  name: 'shiyongfooter',
  components: {
    shoucang
  },
  data () {
    return {
      showshoucang: false,
      mes: '',
      list: [
        {
          id: '020001',
          mianfeiimg: 'static/images/erhuan.jpg',
          icon: 'static/icon/taobao.png',
          title: '【限时免单】【预售】几何菱形显瘦耳环新款1对',
          jiazhi: '29.60',
          xianliang: '39',
          shenqingnum: '26',
          shengyujinbiduihuan: '2'
        },
        {
          id: '020002',
          mianfeiimg: 'static/images/shouchuan.jpg',
          icon: 'static/icon/tianmao.png',
          title: '【限时免单】白玉菩提手串1串',
          jiazhi: '48.00',
          xianliang: '10',
          shenqingnum: '30',
          'shengyujinbiduihuan': '5'
        },
        {
          id: '020003',
          mianfeiimg: 'static/images/dxrchenshan.jpg',
          icon: 'static/icon/taobao.png',
          title: '【限时免单】灯芯绒衬衫女宽松长袖一件',
          jiazhi: '29.90',
          xianliang: '12',
          shenqingnum: '22',
          'shengyujinbiduihuan': '0'
        },
        {
          id: '020004',
          mianfeiimg: 'static/images/jydzhiwu.jpg',
          icon: 'static/icon/taobao.png',
          title: '【限时免单】静夜多肉植物1颗+种子千粒',
          jiazhi: '9.00',
          xianliang: '14',
          shenqingnum: '209',
          'shengyujinbiduihuan': '7'
        },
        {
          id: '020005',
          mianfeiimg: 'static/images/xgshuiyi.jpg',
          icon: 'static/icon/taobao.png',
          title: '【限时免单】粉色透明性感睡衣一件',
          jiazhi: '29.90',
          xianliang: '10',
          shenqingnum: '172',
          'shengyujinbiduihuan': '12'
        },
        {
          id: '020006',
          mianfeiimg: 'static/images/wanlianp.jpg',
          icon: 'static/icon/taobao.png',
          title: '【限时免单】2份碗莲种子2份盆泥液',
          jiazhi: '9.90',
          xianliang: '10',
          shenqingnum: '15',
          'shengyujinbiduihuan': '3'
        },
        {
          id: '020007',
          mianfeiimg: 'static/images/kouhong.jpg',
          icon: 'static/icon/taobao.png',
          title: '【限时免单】芭比粉口红1支',
          jiazhi: '13.80',
          xianliang: '10',
          shenqingnum: '9',
          'shengyujinbiduihuan': '0'
        },
        {
          id: '020008',
          mianfeiimg: 'static/images/hongkuaizi.jpg',
          icon: 'static/icon/jingdong.png',
          title: '【限时免单】精品红木筷子一双',
          jiazhi: '68.00',
          xianliang: '30',
          shenqingnum: '263',
          'shengyujinbiduihuan': '1'
        },
        {
          id: '020009',
          mianfeiimg: 'static/images/qidian.jpg',
          icon: 'static/icon/jingdong.png',
          title: '【限时免单】温度光感水润气垫BB霜15g',
          jiazhi: '59.00',
          xianliang: '18',
          shenqingnum: '207',
          'shengyujinbiduihuan': '11'
        },
        {
          id: '020010',
          mianfeiimg: 'static/images/dadiku.jpg',
          icon: 'static/icon/taobao.png',
          title: '【限时免单】女士打底裤一件',
          jiazhi: '9.90',
          xianliang: '40',
          shenqingnum: '152',
          'shengyujinbiduihuan': '4'
        },
        {
          id: '020011',
          mianfeiimg: 'static/images/wanlianh.jpg',
          icon: 'static/icon/taobao.png',
          title: '【限时免单】2份碗莲种子2份盆泥液',
          jiazhi: '9.90',
          xianliang: '10',
          shenqingnum: '5',
          'shengyujinbiduihuan': '0'
        }
      ]
    }
  },
  methods: {
    toshouye () {
      this.$router.push('/')
    },
    shoucangclick () {
      if (JSON.parse(localStorage.getItem('userinfo')) === null) {
        this.$router.push('/login')
        return false
      } else {
        let SYcollectId = this.$route.params.id
        this.SYCollectId(SYcollectId)
        this.showshoucang = true
        setTimeout(() => { this.showshoucang = false }, 3000)
      }
    },
    ...mapMutations(['SYCollectId']),
    mianfeiclick () {
      if (JSON.parse(localStorage.getItem('userinfo')) === null) {
        this.$router.push('/login')
        return false
      } else {
        this.$router.push('/qianggoudingdan')
      }
    }
  },
  computed: {
    iscollect () {
      let id = this.$route.params.id
      if (this.$store.state.SYcollectId.indexOf(id) === -1) {
        return true
      } else {
        return false
      }
    },
    shoucangtitle () {
      return function (mes) {
        if (this.iscollect === true) {
          return '已取消'
        } else {
          return '收藏成功'
        }
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
.shiyongfooter
  width 100%
  height 2.5rem
  background #fff
  position fixed
  bottom 0
  left 0
  display flex
  flex-direction row
  border-top solid .1px #eee
  .left-footer
    width 100%
    flex 4
    display flex
    flex-direction row
    justify-content space-around
    align-items center
    .shouye,.kefu,.shoucang
      width 33.33%
      height 2.5rem
      display flex
      flex-direction column
      justify-content center
      align-items center
      .iconfont
        font-size 1rem
      .icon-kefu
        font-size 1.1rem
      .icon-shouye
        font-size 1.2rem
      .icon-shc
        font-size 1.2rem
        color #ff7600
    .shouye p,.kefu p,.shoucang p
      font-size .7rem
      padding-top .2rem
  .mianfeishenqing,.jinbiduihuan
    flex 3
    color #fff
    background #e6072a
    font-size .9rem
    display flex
    justify-content center
    align-items center
  .jinbiduihuan
    background #eeab17
  .yiduiwan
    background #c5c0c0
</style>



